.design-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 4;
  overflow: hidden;
  transition: opacity 0.5s;

  .modal-container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 2%;
    padding: 2rem 1rem 4rem 2rem;
    height: 85vh;
    width: 60%;
    background-color: #1a1a1a;
    color: white;
    border-radius: 10px;
    transition: all 0.5s ease;
    z-index: 5;
  }

  .design-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 1rem;
    gap: 2rem;
    overflow: auto;
    h1 {
      font-family: 'Calibre-SB';
      font-size: 3.6rem;
      margin-top: 1rem;
    }
    h2 {
      font-family: 'Calibre-SB';
      font-size: rem;
      color: lightgrey;
    }
    p {
      line-height: 2.5rem;
      font-size: 1.6rem;
      margin: 0 10%;
    }
    a {
      text-decoration: underline;
      text-underline-offset: 0.4rem;
      text-decoration-thickness: 1px;
      text-decoration-color: inherit;
    }
    img {
      width: 100%;
      margin-top: 1.5rem;
    }
  }
}

// ================== HIDE/SHOW MODAL

.design-modal-invisible {
  opacity: 0;
  pointer-events: none;
}

.design-modal-visible {
  opacity: 1;
  pointer-events: initial;
}

// ================== CLOSE BUTTON

.close-container {
  position: absolute;
  width: 20px;
  height: 20px;
  cursor: pointer;
  bottom: 2.75rem;
  right: 1.25rem;
}

.leftright {
  height: 4px;
  width: 20px;
  position: absolute;
  margin-top: 24px;
  background-color: #ff0011;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 0.3s ease-in;
}

.rightleft {
  height: 4px;
  width: 20px;
  position: absolute;
  margin-top: 24px;
  background-color: #ff0011;
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all 0.3s ease-in;
}

// ================== PTP MODAL

.ptp-imgs {
  .ptp-gifs {
    display: flex;
    gap: 1.5rem;
    height: auto;
    img {
      min-width: 48.3%;
    }
  }
}

// ================== SB MODAL

.sb-imgs {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  .sb-row-1,
  .sb-row-2,
  .sb-row-3,
  .sb-row-4 {
    display: flex;
    gap: 2rem;
  }
  img {
    min-width: 48.3%;
  }
  .sb-row-2 {
    img:nth-of-type(2) {
      object-fit: cover;
    }
  }
}

// ================== MEDIA QUERY

@media screen and (max-width: 770px) {
  .design-modal .modal-container {
    width: 70%;
  }
}

@media screen and (max-width: 500px) {
  .design-modal .modal-container {
    width: 90%;
  }
}
